.main {
  background-color: rgb(236, 236, 245);
  height: 100%;
  width: 100%;
  color: black;
  text-align: center;
  padding: 20px;
}
.skillsDiv > div {

  box-shadow: rgb(147, 189, 232) 0px 3px 8px;
  width: 90%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  gap: 10px;
  padding: 20px;
  margin: auto;
}

.head {
  padding: 20px;
}

.Toolhead {
  padding: 20px;
}

.ToolsDiv > div {
  box-shadow: rgb(147, 189, 232) 0px 3px 8px;
  width: 90%;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
  padding: 20px;
  margin: auto;
}
.gitHead {
  margin-top: 70px;
}
.days {
  margin-top: 20px;
}

.gitCalender {
  box-shadow: rgb(147, 189, 232) 0px 3px 8px;

  background-color: rgb(236, 236, 245);
  margin-top: 30px;
  width: 75%;
  padding-left: 25px;
  margin-left: 180px;
  padding-bottom: 20px;
  padding-top: 10px;
}

.stats {
  width: 70%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  margin: auto;
  margin-top: 80px;
}
.stats2 {
  margin-top: 60px;
}

.Toolhead {
  margin-top: 50px;
}

.ToolsimagesDiv > div > p {
  margin-top: 3px;
  width: 100%;
}

.image {
  height: 20vh;
}
.chakra {
  height: 17vh;
}

.Images {
  height: 20vh;
}

.Images2 {
  height: 20vh;
}

@media screen and (min-width: 500px) and (max-width: 1100px) {
  .imagesDiv > div > img {
    height: 70%;
    width: 60%;
  }
  .ToolsimagesDiv > div > img {
    height: 70%;
    width: 60%;
  }
  .gitCalender {
    width: 80%;
    margin: auto;
    padding: 30px;
  }
  .githubimage {
    block-size: 13px;
    margin-block: 13px;
  }
  .stats {
    grid-template-columns: repeat(1, 1fr);
    gap: 40px;
  }
 
  .stats2 {
    width: 40%;
  }

}

@media screen and (min-width: 100px) and (max-width: 500px) {
  .skillsDiv > div {
    flex-direction: column;
    margin: auto;
    padding: 30px;
    width: 85%;
  }


  .ToolsDiv > div {
    flex-direction: column;
    margin: auto;
    width: 85%;
  }
  .ToolsimagesDiv > div > img {
    height: 65%;
    width: 50%;
  }
  .gitCalender {
    width: 85%;
    margin: auto;
    padding: 30px;
    font-size: 11px;
  }
  .githubimage {
    block-size: 13px;
    font-size: 14px;
    height: 40%;
  }
  .stats {
    grid-template-columns: repeat(1, 1fr);
    gap: 40px;
  }
  .image {
    width: 90%;
  }
  .stats2 {
    width: 50%;
  }
  .ToolsimagesDiv > div {
    margin-top: 22px;
  }
}
