.main {
  background-color: rgb(236, 236, 245);
  height: 100%;
  width: 100%;
}

.head {
  text-align: center;
  width: 100%;
  padding: 15px;
  color: black;
}
.imgDiv {
  top: 0;
  bottom: 0;

  height: 100%;
  width: 100%;

  filter: blur(2px);
  -webkit-filter: blur(2px);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.girl {
  height: 100%;
  width: 100%;
}

.name {
  background-color: rgb(236, 236, 245);
  width: 50%;
  box-shadow: rgb(147, 189, 232) 0px 3px 8px;
  padding: 15px;
  position: absolute;
  margin-top: -650px;
  color: white;
  font-weight: bold;
  font-size: 23px;
  text-align: center;
}

@media screen and (min-width: 100px) and (max-width: 500px) {
  .main {
    width: 100%;
    flex-direction: column-reverse;
    margin-top: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
  }
  .name {
    padding: 30px;
    width: 100%;
    margin-top: 2px;
    margin-top: 570px;
  }

  .head {
    text-align: center;
    margin: auto;
  }
}

@media screen and (min-width: 500px) and (max-width: 1100px) {
  .main {
    margin-top: 70px;
    width: 100%;
    flex-direction: column-reverse;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .imgDiv {
    width: 100%;
  }
  .name {
    padding: 30px;
    width: 100%;
    margin-top: 2px;
  }

  .head {
    text-align: center;
    margin: auto;
  }
}
