.main {
  background-color: rgb(236, 236, 245);
  height: 100%;
  width: 100%;
  text-align: center;
  padding: 30px;
}

.subDiv {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
  margin: auto;
  padding: 10px;
}

.all {

  margin: auto;
  width: 33%;
  padding: 10px;
}

.made {
  padding: 10px;
  margin-top: 10px;
}

.get{
  padding:10px;
}




@media screen and (min-width: 500px) and (max-width: 1100px) {
  .all {
    width: 69%;
  }
}

@media screen and (min-width: 100px) and (max-width: 500px) {
  .subDiv {
    width: 100%;
    flex-direction: column;
  }

  .all {
   
    width: 100%;
  }
}
