.main {
  height: 100%;
  width: 100%;
  background-color: rgb(236, 236, 245);
  color: black;
  padding: 20px;
}
.mainhead {
  color: black;
  text-align: center;
  margin-top: 30px;
}

.project {
  box-shadow: rgb(147, 189, 232) 0px 3px 8px;
  width: 90%;
  height: 500px;
  margin: auto;
  color: black;
  margin-top: 40px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 30px;
}

.bloommedplus,
.crystallvills,
.lyst,
.musicose {
  height: 100%;
  width: 100%;
  display: block;
}

.description {
  width: 90%;
  height: 490px;
  text-align: center;
  padding: 20px;
}

.head {
  margin-top: 10px;
  color: black;
}

.prjctDes1 {
  margin-top: 30px;
}

.tech {
  margin-top: 30px;
  color: black;
}

.lists {
  width: 50%;
  margin: auto;
}

.techs {
  margin: 5px;
  display: flex;
  padding: 5px;
  gap: 20px;
}

.techs > h4 {
  box-shadow: rgb(147, 189, 232) 0px 3px 8px;
  width: 80%;
  margin: auto;
  padding: 5px;
  margin-top: 20px;
}

.links {
  margin-top: 50px;
  width: 100%;
  display: flex;
  justify-content: space-evenly;
}

.githubLink,
.link,
.vdo {
  border: 2px solid #1e90ff;
  background-color: black;
  color: white;

  width: 100px;
  height: 40px;

  font-size: 20px;
  cursor: pointer;
}

.Imgdiv {
  height: 100%;
  margin: auto;
}

.redux {
  box-shadow: rgb(147, 189, 232) 0px 3px 8px;
  width: 18%;
  margin: 5px;
  height: 30px;
  margin: auto;
  padding: 3px;
}

@media screen and (min-width: 500px) and (max-width: 1100px) {
  .project {
    flex-direction: column;
    height: 100%;
  }
  
}

@media screen and (min-width: 100px) and (max-width: 500px) {
  .project {
    flex-direction: column;
    height: 100%;
  }
  .bloommedplus,
  .crystallvills,
  .lyst {
    width: 100%;
  }
  .description {
    margin: auto;
    height: 100%;
  }
  .techs {
    flex-direction: column;
  }
  .redux{
    width: 73%;
    margin-top: 20px;
  }
  .links{
    flex-direction: column;
    gap: 10px;
  }
}
